@import url(https://use.typekit.net/woy4mbd.css);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@800&display=swap);
html{font-size:14px}body{font-family:stratos-lights, sans-serif !important;font-weight:400 !important;font-style:normal;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1,h2,h3{font-family:orpheuspro, serif !important;font-weight:500 !important;font-style:normal;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.viewport-fill{width:100%;height:100vh}.vertical-center{display:flex;align-items:center}@media (min-width: 320px){html{font-size:calc(14px + 4 * (100vw - 320px) / 1046)}}@media (min-width: 1366px){html{font-size:18px}}

.App{font-weight:400;color:#000}.App h1{font-weight:600}.App a{color:#000;text-decoration:none !important}*{margin:0;padding:0;box-sizing:border-box}header{display:flex;padding:10px 5%;background-color:#ddd1c3;justify-content:space-between;align-items:center}#logo{font-family:'Nanum Myeongjo', serif !important;font-weight:800 !important;font-size:2em}.inline ul{margin:0;padding:0;list-style-type:none}.inline li{display:inline-block}nav{text-transform:uppercase}nav li{padding:0 1em}#footer{text-align:center;padding-bottom:1.25em}#footer #mobile-links{display:none;justify-content:center}#footer #mobile-links li{padding:0 0.5em}@media (max-width: 768px){#footer #mobile-links{display:flex}}@media (prefers-color-scheme: dark){.App{color:#ddd1c3;background-color:#080808}a{color:#ddd1c3 !important}header{background-color:#080808}#footer #mobile-links{-webkit-filter:invert(90%) sepia(6%) saturate(492%) hue-rotate(352deg) brightness(95%) contrast(89%);filter:invert(90%) sepia(6%) saturate(492%) hue-rotate(352deg) brightness(95%) contrast(89%)}}@media (prefers-color-scheme: light){.App{color:#000;background-color:#ddd1c3}a{color:#000 !important}header{background-color:#ddd1c3}}

.links{position:fixed;z-index:5;right:3%;bottom:0;align-items:center;padding-top:0;list-style-type:none;list-style-position:outside;list-style-position:initial;list-style-image:none;list-style-image:initial}.links ul{display:flex;flex-direction:column}.links ul li{margin:0.5em 0;list-style:none}.links::after{content:'';display:block;width:2px;height:3em;margin:0 auto 0 0.6em;background-color:#000}.hvr-grow{display:inline-block;vertical-align:middle;transform:translateZ(0);box-shadow:0 0 1px rgba(0,0,0,0);-webkit-backface-visibility:hidden;backface-visibility:hidden;-moz-osx-font-smoothing:grayscale;transform:scale3d(1, 1, 1);transition-duration:0.5s;transition-property:transform}.hvr-grow:hover,.hvr-grow:focus,.hvr-grow:active{transform:scale3d(1.5, 1.5, 1.5)}@media (max-width: 768px){.links{display:none}}@media (prefers-color-scheme: dark){.links{-webkit-filter:invert(90%) sepia(6%) saturate(492%) hue-rotate(352deg) brightness(95%) contrast(89%);filter:invert(90%) sepia(6%) saturate(492%) hue-rotate(352deg) brightness(95%) contrast(89%)}}@media (prefers-color-scheme: light){.links::after{background-color:#000}}

#intro-heading{font-size:2.5em}#pronouns{margin:0;font-size:0.45em;color:#757575}#intro-content{font-size:1.25em;padding-right:40%}@media (prefers-color-scheme: dark){#pronouns{color:#afaeae}}@media (prefers-color-scheme: light){#pronouns{color:#757575}}

.project{display:flex;max-width:100%;height:auto;margin:50px auto;border-radius:25px;justify-content:center;align-items:center;overflow:hidden}.hvr-grow{display:inline-block;vertical-align:middle;transform:translateZ(0);box-shadow:0 0 1px rgba(0,0,0,0);-webkit-backface-visibility:hidden;backface-visibility:hidden;-moz-osx-font-smoothing:grayscale;transform:scale3d(1, 1, 1);transition-duration:0.5s;transition-property:transform}.hvr-grow:hover,.hvr-grow:focus,.hvr-grow:active{transform:scale3d(1.025, 1.025, 1.025)}.project-info{margin:150px 20px auto auto;padding-left:20%;text-align:right}.project-info h2{padding-bottom:0.25em;font-weight:600}.project-info ul li{display:inline-block;margin:5px;font-size:0.9em;color:#757575;list-style-type:none}.link{display:flex;font-weight:500 !important;justify-content:center;align-items:center}@media (max-width: 768px){.project-info{display:none}}@media (prefers-color-scheme: dark){.project-info ul li{color:#afaeae}}@media (prefers-color-scheme: light){.project-info ul li{color:#757575}}

#bio{font-size:1em}#portrait{display:flex;max-width:275px;height:auto;margin:auto;padding:5px;border:1px solid;border-radius:50%;justify-content:center;align-items:center}@media (max-width: 768px){#portrait{display:none}}

